import { useEffect, useRef, useCallback } from 'react';
import garbageCollectionService, { GCMetrics } from '../services/GarbageCollectionService';

interface UseGarbageCollectionOptions {
  componentName?: string;
  autoRegister?: boolean;
  priority?: 'high' | 'medium' | 'low';
}

export const useGarbageCollection = (
  cleanup?: () => void,
  options: UseGarbageCollectionOptions = {}
) => {
  const {
    componentName = 'UnknownComponent',
    autoRegister = true,
    priority = 'medium'
  } = options;
  
  const cleanupIdRef = useRef<string | null>(null);
  const mountTimeRef = useRef(Date.now());

  // Register cleanup function
  const registerCleanup = useCallback((cleanupFn: () => void, description?: string) => {
    const id = `${componentName}_${mountTimeRef.current}_${Math.random().toString(36).substr(2, 9)}`;
    cleanupIdRef.current = id;
    
    garbageCollectionService.registerCleanup(
      id,
      cleanupFn,
      priority,
      description || `${componentName} cleanup`
    );
    
    return id;
  }, [componentName, priority]);

  // Unregister cleanup function
  const unregisterCleanup = useCallback(() => {
    if (cleanupIdRef.current) {
      garbageCollectionService.unregisterCleanup(cleanupIdRef.current);
      cleanupIdRef.current = null;
    }
  }, []);

  // Force garbage collection
  const forceGC = useCallback(() => {
    return garbageCollectionService.forceGC();
  }, []);

  // Get GC metrics
  const getMetrics = useCallback(() => {
    return garbageCollectionService.getMetrics();
  }, []);

  // Auto-register cleanup on mount
  useEffect(() => {
    if (autoRegister && cleanup) {
      registerCleanup(cleanup);
    }
    
    // Always unregister on unmount
    return () => {
      unregisterCleanup();
    };
  }, [autoRegister, cleanup, registerCleanup, unregisterCleanup]);

  return {
    registerCleanup,
    unregisterCleanup,
    forceGC,
    getMetrics
  };
};

// Hook for monitoring memory usage with automatic cleanup
export const useMemoryCleanup = (
  componentName: string,
  thresholdMB = 100
) => {
  const { forceGC, getMetrics } = useGarbageCollection(undefined, {
    componentName,
    autoRegister: false
  });

  useEffect(() => {
    const checkMemory = () => {
      if ('memory' in performance && (performance as any).memory) {
        const memoryMB = (performance as any).memory.usedJSMemorySize / (1024 * 1024);
        
        if (memoryMB > thresholdMB) {
          console.warn(`🚨 Memory usage high in ${componentName}: ${memoryMB.toFixed(2)}MB - forcing cleanup`);
          forceGC();
        }
      }
    };

    const interval = setInterval(checkMemory, 10000); // Check every 10 seconds
    
    return () => clearInterval(interval);
  }, [componentName, thresholdMB, forceGC]);

  return { forceGC, getMetrics };
};

// Hook for registering DOM element cleanup
export const useDOMCleanup = (elementRef: React.RefObject<HTMLElement>, componentName: string) => {
  const { registerCleanup } = useGarbageCollection(undefined, {
    componentName,
    autoRegister: false,
    priority: 'high'
  });

  useEffect(() => {
    if (elementRef.current) {
      const element = elementRef.current;
      
      const cleanupId = registerCleanup(() => {
        // Remove all event listeners
        const clone = element.cloneNode(true);
        if (element.parentNode) {
          element.parentNode.replaceChild(clone, element);
        }
        
        // Clear any data attributes
        if (element.dataset) {
          Object.keys(element.dataset).forEach(key => {
            delete element.dataset[key];
          });
        }
        
        // Remove from DOM if still attached
        if (element.isConnected && element.parentNode) {
          element.parentNode.removeChild(element);
        }
      }, `DOM cleanup for ${componentName}`);
      
      return () => {
        garbageCollectionService.unregisterCleanup(cleanupId);
      };
    }
  }, [elementRef, componentName, registerCleanup]);
};

// Hook for registering interval/timeout cleanup
export const useTimerCleanup = (componentName: string) => {
  const timersRef = useRef<Set<NodeJS.Timeout>>(new Set());
  const { registerCleanup } = useGarbageCollection(undefined, {
    componentName,
    autoRegister: false
  });

  const createTimer = useCallback((
    type: 'timeout' | 'interval',
    callback: () => void,
    delay: number
  ) => {
    const timer = type === 'timeout' 
      ? setTimeout(callback, delay)
      : setInterval(callback, delay);
    
    timersRef.current.add(timer);
    
    // Auto cleanup function
    const cleanup = () => {
      if (type === 'timeout') {
        clearTimeout(timer);
      } else {
        clearInterval(timer);
      }
      timersRef.current.delete(timer);
    };

    return { timer, cleanup };
  }, []);

  // Register cleanup for all timers
  useEffect(() => {
    const cleanupId = registerCleanup(() => {
      timersRef.current.forEach(timer => {
        clearTimeout(timer);
        clearInterval(timer);
      });
      timersRef.current.clear();
    }, `Timer cleanup for ${componentName}`);

    return () => {
      garbageCollectionService.unregisterCleanup(cleanupId);
    };
  }, [componentName, registerCleanup]);

  return { createTimer };
};

export default useGarbageCollection;