import { useEffect, useRef, useState } from 'react';

interface MemoryStats {
  usedJSMemorySize?: number;
  totalJSMemorySize?: number;
  jsMemoryLimit?: number;
  timestamp: number;
}

interface PerformanceMetrics {
  memoryUsage: MemoryStats[];
  isMonitoring: boolean;
  averageMemoryUsage: number;
  memoryTrend: 'increasing' | 'decreasing' | 'stable';
}

const useMemoryMonitor = (interval = 5000): PerformanceMetrics => {
  const [memoryUsage, setMemoryUsage] = useState<MemoryStats[]>([]);
  const [isMonitoring, setIsMonitoring] = useState(false);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  const getMemoryInfo = (): MemoryStats => {
    const timestamp = Date.now();
    
    // Type guard for Chrome-specific performance.memory
    if ('memory' in performance && performance.memory) {
      const memory = performance.memory as any;
      return {
        usedJSMemorySize: memory.usedJSMemorySize,
        totalJSMemorySize: memory.totalJSMemorySize,
        jsMemoryLimit: memory.jsMemoryLimit,
        timestamp
      };
    }
    
    return { timestamp };
  };

  const startMonitoring = () => {
    if (intervalRef.current) return;
    
    setIsMonitoring(true);
    
    // Initial measurement
    setMemoryUsage(prev => [...prev, getMemoryInfo()]);
    
    intervalRef.current = setInterval(() => {
      setMemoryUsage(prev => {
        const newEntry = getMemoryInfo();
        // Keep only last 50 measurements to prevent memory growth
        const updated = [...prev.slice(-49), newEntry];
        return updated;
      });
    }, interval);
  };

  const stopMonitoring = () => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }
    setIsMonitoring(false);
  };

  const clearHistory = () => {
    setMemoryUsage([]);
  };

  // Calculate average memory usage
  const averageMemoryUsage = memoryUsage.length > 0 
    ? memoryUsage.reduce((sum, entry) => {
        return sum + (entry.usedJSMemorySize || 0);
      }, 0) / memoryUsage.length
    : 0;

  // Calculate memory trend
  const memoryTrend = (() => {
    if (memoryUsage.length < 5) return 'stable';
    
    const recent = memoryUsage.slice(-5);
    const first = recent[0]?.usedJSMemorySize || 0;
    const last = recent[recent.length - 1]?.usedJSMemorySize || 0;
    
    const change = last - first;
    const threshold = first * 0.05; // 5% threshold
    
    if (change > threshold) return 'increasing';
    if (change < -threshold) return 'decreasing';
    return 'stable';
  })();

  // Auto-start monitoring in development
  useEffect(() => {
    if (process.env.NODE_ENV === 'development') {
      startMonitoring();
    }
    
    return () => {
      stopMonitoring();
    };
  }, []);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, []);

  return {
    memoryUsage,
    isMonitoring,
    averageMemoryUsage,
    memoryTrend
  };
};

export default useMemoryMonitor;
export type { MemoryStats, PerformanceMetrics };