import { useRef, useEffect, useCallback } from 'react';
import { TerminalBatcher } from '../services/WebSocketBatcher';
import { Socket } from 'socket.io-client';

interface UseWebSocketBatchingProps {
  socket: Socket | null;
  enabled?: boolean;
}

interface BatchedSocketEmitter {
  emit: (event: string, data: any, priority?: 'high' | 'medium' | 'low') => void;
  forceFlush: () => void;
  getQueueSize: () => number;
}

export const useWebSocketBatching = ({ 
  socket, 
  enabled = true 
}: UseWebSocketBatchingProps): BatchedSocketEmitter => {
  const batcherRef = useRef<TerminalBatcher | null>(null);
  
  // Initialize batcher when socket is available
  useEffect(() => {
    if (!socket || !enabled) {
      return;
    }
    
    const socketEmit = (event: string, data: any) => {
      socket.emit(event, data);
    };
    
    batcherRef.current = new TerminalBatcher(socketEmit);
    
    // Cleanup on unmount or socket change
    return () => {
      if (batcherRef.current) {
        batcherRef.current.destroy();
        batcherRef.current = null;
      }
    };
  }, [socket, enabled]);
  
  const emit = useCallback((
    event: string, 
    data: any, 
    priority: 'high' | 'medium' | 'low' = 'medium'
  ) => {
    if (batcherRef.current && enabled) {
      batcherRef.current.addMessage(event, data, priority);
    } else if (socket) {
      // Fallback to direct emission if batching is disabled
      socket.emit(event, data);
    }
  }, [socket, enabled]);
  
  const forceFlush = useCallback(() => {
    if (batcherRef.current) {
      batcherRef.current.forceFlush();
    }
  }, []);
  
  const getQueueSize = useCallback(() => {
    return batcherRef.current?.getQueueSize() || 0;
  }, []);
  
  return {
    emit,
    forceFlush,
    getQueueSize
  };
};

export default useWebSocketBatching;