interface BatchedMessage {
  id: string;
  event: string;
  data: any;
  timestamp: number;
  priority: 'high' | 'medium' | 'low';
}

interface BatchConfig {
  maxBatchSize: number;
  batchTimeoutMs: number;
  highPriorityTimeoutMs: number;
  maxQueueSize: number;
}

type MessageHandler = (messages: BatchedMessage[]) => void;

export class WebSocketBatcher {
  private queue: BatchedMessage[] = [];
  private batchTimeout: NodeJS.Timeout | null = null;
  private config: BatchConfig;
  private handler: MessageHandler;
  private messageCount = 0;
  
  constructor(handler: MessageHandler, config?: Partial<BatchConfig>) {
    this.handler = handler;
    this.config = {
      maxBatchSize: 10,
      batchTimeoutMs: 50, // 50ms batching for better responsiveness
      highPriorityTimeoutMs: 10, // High priority messages get faster processing
      maxQueueSize: 1000,
      ...config
    };
  }
  
  public addMessage(event: string, data: any, priority: 'high' | 'medium' | 'low' = 'medium'): void {
    // Prevent queue overflow
    if (this.queue.length >= this.config.maxQueueSize) {
      console.warn('WebSocket batch queue full, dropping oldest messages');
      this.queue = this.queue.slice(-this.config.maxQueueSize + 1);
    }
    
    const message: BatchedMessage = {
      id: `msg_${this.messageCount++}_${Date.now()}`,
      event,
      data,
      timestamp: Date.now(),
      priority
    };
    
    this.queue.push(message);
    
    // High priority messages trigger immediate processing
    if (priority === 'high') {
      this.scheduleFlush(this.config.highPriorityTimeoutMs);
    } else {
      this.scheduleFlush(this.config.batchTimeoutMs);
    }
    
    // Flush when batch size is reached
    if (this.queue.length >= this.config.maxBatchSize) {
      this.flush();
    }
  }
  
  private scheduleFlush(timeoutMs: number): void {
    if (this.batchTimeout) {
      return; // Already scheduled
    }
    
    this.batchTimeout = setTimeout(() => {
      this.flush();
    }, timeoutMs);
  }
  
  private flush(): void {
    if (this.batchTimeout) {
      clearTimeout(this.batchTimeout);
      this.batchTimeout = null;
    }
    
    if (this.queue.length === 0) {
      return;
    }
    
    // Sort by priority and timestamp
    const sortedMessages = [...this.queue].sort((a, b) => {
      const priorityOrder = { high: 0, medium: 1, low: 2 };
      const priorityDiff = priorityOrder[a.priority] - priorityOrder[b.priority];
      return priorityDiff !== 0 ? priorityDiff : a.timestamp - b.timestamp;
    });
    
    this.queue = [];
    
    try {
      this.handler(sortedMessages);
    } catch (error) {
      console.error('Error handling batched messages:', error);
    }
  }
  
  public forceFlush(): void {
    this.flush();
  }
  
  public getQueueSize(): number {
    return this.queue.length;
  }
  
  public destroy(): void {
    this.flush();
    if (this.batchTimeout) {
      clearTimeout(this.batchTimeout);
      this.batchTimeout = null;
    }
    this.queue = [];
  }
}

// Terminal-specific batching service
export class TerminalBatcher extends WebSocketBatcher {
  constructor(socketEmit: (event: string, data: any) => void) {
    super(
      (messages) => {
        // Group messages by event type for efficient processing
        const grouped = new Map<string, any[]>();
        
        messages.forEach(msg => {
          if (!grouped.has(msg.event)) {
            grouped.set(msg.event, []);
          }
          grouped.get(msg.event)!.push(msg.data);
        });
        
        // Send grouped messages
        grouped.forEach((dataArray, event) => {
          if (event === 'terminal:input' && dataArray.length > 1) {
            // Combine terminal input for better performance
            const combinedInput = dataArray.join('');
            socketEmit(event, { data: combinedInput });
          } else {
            // Send individually for other events
            dataArray.forEach(data => socketEmit(event, data));
          }
        });
      },
      {
        maxBatchSize: 5, // Smaller batches for terminal responsiveness
        batchTimeoutMs: 16, // ~60fps batching
        highPriorityTimeoutMs: 5, // Very fast for critical terminal events
        maxQueueSize: 500
      }
    );
  }
}

export default WebSocketBatcher;