/**
 * Magic MCP UI Service - Enhanced with React Bits Integration and Variations Support
 * TypeScript wrapper for AI-powered component generation
 */

interface MagicProgress {
  status: 'idle' | 'generating' | 'complete' | 'error';
  message?: string;
  component?: any;
}

interface MagicComponent {
  success: boolean;
  componentCode: string;
  name: string;
  explanation?: string;
  metadata?: {
    source: string;
    searchQuery?: string;
    timestamp: string;
    componentType?: string;
    theme?: string;
    quality?: string;
  };
}

interface MagicVariation {
  id: number;
  code: string;
  name: string;
  explanation: string;
  metadata?: {
    source: string;
    componentType?: string;
    theme?: string;
    quality?: string;
  };
  variationIndex: number;
  generatedAt: string;
  note?: string;
}

interface MagicVariationsResponse {
  success: boolean;
  variations: MagicVariation[];
  total: number;
  prompt: string;
  generatedAt: string;
}

interface MagicGenerationOptions {
  message: string;
  searchQuery?: string;
  currentFilePath?: string;
  projectDirectory?: string;
}

