interface CleanupFunction {
  id: string;
  cleanup: () => void;
  priority: 'high' | 'medium' | 'low';
  description: string;
  createdAt: number;
}

interface GCMetrics {
  totalCleanups: number;
  activeCleanups: number;
  lastGCTime: number;
  gcDuration: number;
  memoryFreed: number;
  errors: number;
}

class GarbageCollectionService {
  private cleanupRegistry = new Map<string, CleanupFunction>();
  private gcInterval: NodeJS.Timeout | null = null;
  private metrics: GCMetrics = {
    totalCleanups: 0,
    activeCleanups: 0,
    lastGCTime: 0,
    gcDuration: 0,
    memoryFreed: 0,
    errors: 0
  };
  private isGCRunning = false;
  private config = {
    gcIntervalMs: 30000, // 30 seconds
    maxCleanupAge: 300000, // 5 minutes
    memoryThreshold: 50 * 1024 * 1024, // 50MB
    enableAutoGC: true,
    logLevel: process.env.NODE_ENV === 'development' ? 'verbose' : 'errors'
  };

  constructor() {
    this.startAutoGC();
    this.setupEventListeners();
  }

  // Register a cleanup function
  public registerCleanup(
    id: string,
    cleanup: () => void,
    priority: 'high' | 'medium' | 'low' = 'medium',
    description = 'Unknown cleanup'
  ): void {
    const cleanupFunction: CleanupFunction = {
      id,
      cleanup,
      priority,
      description,
      createdAt: Date.now()
    };

    this.cleanupRegistry.set(id, cleanupFunction);
    this.metrics.activeCleanups = this.cleanupRegistry.size;

    if (this.config.logLevel === 'verbose') {
      console.log(`🧹 Registered cleanup: ${id} (${description})`);
    }
  }

  // Unregister and run a specific cleanup
  public unregisterCleanup(id: string): boolean {
    const cleanup = this.cleanupRegistry.get(id);
    if (!cleanup) {
      return false;
    }

    try {
      cleanup.cleanup();
      this.cleanupRegistry.delete(id);
      this.metrics.totalCleanups++;
      this.metrics.activeCleanups = this.cleanupRegistry.size;

      if (this.config.logLevel === 'verbose') {
        console.log(`✅ Executed cleanup: ${id} (${cleanup.description})`);
      }
      
      return true;
    } catch (error) {
      this.metrics.errors++;
      console.error(`❌ Cleanup failed for ${id}:`, error);
      return false;
    }
  }

  // Force garbage collection
  public forceGC(): Promise<GCMetrics> {
    return new Promise((resolve) => {
      if (this.isGCRunning) {
        resolve(this.metrics);
        return;
      }

      this.runGarbageCollection().then(() => {
        resolve(this.metrics);
      });
    });
  }

  // Run garbage collection cycle
  private async runGarbageCollection(): Promise<void> {
    if (this.isGCRunning) return;

    this.isGCRunning = true;
    const startTime = performance.now();
    const memoryBefore = this.getMemoryUsage();

    try {
      // Clean up expired functions
      await this.cleanupExpiredFunctions();
      
      // Clean up DOM elements
      await this.cleanupDOMElements();
      
      // Clean up event listeners
      await this.cleanupEventListeners();
      
      // Clean up cached data
      await this.cleanupCaches();
      
      // Force browser garbage collection if available
      this.forceBrowserGC();

      const memoryAfter = this.getMemoryUsage();
      const duration = performance.now() - startTime;

      this.metrics.lastGCTime = Date.now();
      this.metrics.gcDuration = duration;
      this.metrics.memoryFreed = Math.max(0, memoryBefore - memoryAfter);

      if (this.config.logLevel === 'verbose') {
        console.log(`🧹 GC completed in ${duration.toFixed(2)}ms, freed ${this.formatBytes(this.metrics.memoryFreed)}`);
      }

    } catch (error) {
      this.metrics.errors++;
      console.error('🚨 Garbage collection error:', error);
    } finally {
      this.isGCRunning = false;
    }
  }

  // Clean up expired cleanup functions
  private async cleanupExpiredFunctions(): Promise<void> {
    const now = Date.now();
    const expiredIds: string[] = [];

    Array.from(this.cleanupRegistry.entries()).forEach(([id, cleanup]) => {
      if (now - cleanup.createdAt > this.config.maxCleanupAge) {
        expiredIds.push(id);
      }
    });

    for (const id of expiredIds) {
      this.unregisterCleanup(id);
    }
  }

  // Clean up orphaned DOM elements
  private async cleanupDOMElements(): Promise<void> {
    // Remove orphaned Monaco editor instances
    const orphanedEditors = document.querySelectorAll('.monaco-editor:not([data-active])');
    orphanedEditors.forEach(editor => {
      if (editor.parentNode) {
        editor.parentNode.removeChild(editor);
      }
    });

    // Clean up empty containers
    const emptyContainers = document.querySelectorAll('.editor-container:empty, .terminal-container:empty');
    emptyContainers.forEach(container => {
      if (container.parentNode) {
        container.parentNode.removeChild(container);
      }
    });
  }

  // Clean up event listeners
  private async cleanupEventListeners(): Promise<void> {
    // This is more of a preventive measure - track listeners in components
    // and clean them up using the cleanup registry
    
    // Clean up global listeners that might have been orphaned
    const orphanedEvents = ['resize', 'scroll', 'keydown', 'keyup'];
    
    orphanedEvents.forEach(eventType => {
      // We can't actually remove all listeners, but we can log warnings
      // about potential memory leaks
      if (this.config.logLevel === 'verbose') {
        console.log(`🔍 Checking for orphaned ${eventType} listeners`);
      }
    });
  }

  // Clean up various caches
  private async cleanupCaches(): Promise<void> {
    // Clear old entries from localStorage/sessionStorage if they exist
    try {
      const storageKeys = Object.keys(localStorage);
      const oldKeys = storageKeys.filter(key => {
        if (key.startsWith('monaco-') || key.startsWith('terminal-')) {
          try {
            const data = JSON.parse(localStorage.getItem(key) || '{}');
            if (data.timestamp && Date.now() - data.timestamp > 86400000) { // 24 hours
              return true;
            }
          } catch (e) {
            return true; // Remove corrupted data
          }
        }
        return false;
      });

      oldKeys.forEach(key => localStorage.removeItem(key));
      
      if (oldKeys.length > 0 && this.config.logLevel === 'verbose') {
        console.log(`🧹 Cleaned up ${oldKeys.length} old localStorage entries`);
      }
    } catch (error) {
      console.warn('Failed to clean localStorage cache:', error);
    }
  }

  // Force browser garbage collection if available
  private forceBrowserGC(): void {
    if ('gc' in window && typeof (window as any).gc === 'function') {
      try {
        (window as any).gc();
        if (this.config.logLevel === 'verbose') {
          console.log('🧹 Forced browser garbage collection');
        }
      } catch (error) {
        console.warn('Failed to force browser GC:', error);
      }
    }
  }

  // Get current memory usage
  private getMemoryUsage(): number {
    if ('memory' in performance && (performance as any).memory) {
      return (performance as any).memory.usedJSMemorySize || 0;
    }
    return 0;
  }

  // Format bytes for display
  private formatBytes(bytes: number): string {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }

  // Start automatic garbage collection
  private startAutoGC(): void {
    if (!this.config.enableAutoGC) return;

    this.gcInterval = setInterval(() => {
      const memoryUsage = this.getMemoryUsage();
      
      // Run GC if memory usage exceeds threshold or if there are many active cleanups
      if (memoryUsage > this.config.memoryThreshold || this.cleanupRegistry.size > 50) {
        this.runGarbageCollection();
      }
    }, this.config.gcIntervalMs);
  }

  // Setup event listeners for cleanup triggers
  private setupEventListeners(): void {
    // Clean up before page unload
    window.addEventListener('beforeunload', () => {
      this.cleanup();
    });

    // Clean up on visibility change (when tab becomes hidden)
    document.addEventListener('visibilitychange', () => {
      if (document.hidden) {
        this.runGarbageCollection();
      }
    });

    // Clean up on memory pressure warnings if available
    if ('onmemorywarning' in window) {
      (window as any).addEventListener('memorywarning', () => {
        console.warn('🚨 Memory warning received - forcing GC');
        this.forceGC();
      });
    }
  }

  // Get current metrics
  public getMetrics(): GCMetrics {
    return { ...this.metrics };
  }

  // Update configuration
  public updateConfig(newConfig: Partial<typeof this.config>): void {
    this.config = { ...this.config, ...newConfig };
    
    // Restart auto GC if interval changed
    if (newConfig.gcIntervalMs && this.gcInterval) {
      clearInterval(this.gcInterval);
      this.startAutoGC();
    }
  }

  // Clean up the service itself
  public cleanup(): void {
    if (this.gcInterval) {
      clearInterval(this.gcInterval);
      this.gcInterval = null;
    }

    // Run all registered cleanups
    const cleanupIds = Array.from(this.cleanupRegistry.keys());
    cleanupIds.forEach(id => this.unregisterCleanup(id));

    console.log('🧹 Garbage Collection Service cleaned up');
  }
}

// Create singleton instance
const garbageCollectionService = new GarbageCollectionService();

export default garbageCollectionService;
export type { CleanupFunction, GCMetrics };